:root {
  --font-body: "Kumbh Sans", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --verydarkdesaturatedblue: hsl(238, 29%, 16%);
  --softred: hsl(14, 88%, 65%);
  --verydarkgrayishblue: hsl(237, 12%, 33%);
  --darkgrayishblue: hsl(240, 6%, 50%);
  --softviolet: hsl(273, 75%, 66%);
  --softblue: hsl(240, 73%, 65%);
  --lightgrayishblue: hsl(240, 5%, 91%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}
html:focus-within {
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body,
p,
figure,
blockquote,
dl,
dd {
  margin: 0 5rem;
  padding: 0;
}

body {
  min-height: 100vh;
  max-width: rem(1440);
  margin: 0 auto;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  font-family: var(--font-body);
  background: linear-gradient(180deg, var(--softviolet), var(--softblue));
}

img,
picture {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

ul,
ol {
  list-style: none;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  min-height: 100vh;
}

footer {
  font-size: 11px;
  text-align: center;
}
footer a {
  color: var(--lightgrayishblue);
}

.accordion-card {
  display: flex;
  position: relative;
  background-color: #fff;
  flex-direction: column;
  margin: 6rem 2rem;
  width: 100%;
  min-width: 300px;
  max-width: 820px;
  height: 560px;
  border-radius: 25px;
}
@media (min-width: 900px) {
  .accordion-card {
    flex-direction: row;
    max-width: 920px;
    height: 510px;
  }
}
.accordion-card .accordion-box {
  display: flex;
  justify-content: center;
  height: 0;
}
.accordion-card .accordion-box img {
  position: relative;
  right: 70px;
  top: -148px;
  width: 110px;
}
@media (min-width: 900px) {
  .accordion-card .accordion-box img {
    position: absolute;
    width: 200px;
    top: 210px;
    left: -94px;
  }
}

.accordion-images {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-position: 50% 100%;
  background-size: 230px;
  background-image: url("../../../images/bg-pattern-desktop.svg");
  background-color: transparent;
  background-repeat: no-repeat;
  height: 98px;
}
@media (min-width: 900px) {
  .accordion-images {
    margin-top: 6rem;
    flex-direction: row;
    margin-top: 0;
    height: 510px;
    background-position: 131% 62%;
    background-size: 150%;
    align-items: center;
    overflow: hidden;
  }
}
.accordion-images .accordion-woman {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 0;
}
@media (min-width: 900px) {
  .accordion-images .accordion-woman {
    height: 460px;
  }
}
.accordion-images .accordion-woman img {
  width: 230px;
  position: relative;
  top: -17px;
}
@media (min-width: 900px) {
  .accordion-images .accordion-woman img {
    position: relative;
    width: 100%;
    top: 0;
    left: -74px;
  }
}

@media (min-width: 900px) {
  .accordion-images,
.accordion-text {
    flex: 1;
  }
}

@media (min-width: 900px) {
  .accordion-item {
    margin: 0.2rem 0;
  }
}
.accordion-item span {
  border-bottom: 1px solid var(--lightgrayishblue);
  margin-left: 1rem;
  margin-top: 0.25rem;
  width: 75%;
  height: 1px;
  display: block;
}

.accordion-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.accordion-text h1 {
  text-align: center;
  margin-top: 1rem;
  margin-bottom: 2rem;
}
@media (min-width: 900px) {
  .accordion-text h1 {
    text-align: left;
    margin-bottom: 1rem;
    padding-left: 1rem;
  }
}

.accordion-item-header {
  font-size: 13px;
  line-height: 3;
  position: relative;
  cursor: pointer;
  transition: color 250ms ease-out;
  padding-left: 1.5rem;
}
@media (min-width: 900px) {
  .accordion-item-header {
    font-size: 14px;
    padding: 0 6.625rem 0 1rem;
  }
}
.accordion-item-header:hover {
  color: var(--softred);
}
.accordion-item-header::after {
  content: url("../../../images/icon-arrow-down.svg");
  position: absolute;
  top: 0;
  transition: transform 150ms ease-in-out;
  right: 1.5rem;
}
@media (min-width: 900px) {
  .accordion-item-header::after {
    right: 7rem;
  }
}
.accordion-item-header.active {
  font-weight: 700;
}
.accordion-item-header.active + .accordion-item-body {
  display: block;
}
.accordion-item-header.active::after {
  transform: rotate(180deg);
}

.accordion-item-body {
  display: none;
}

.accordion-item-body-content {
  font-size: 12px;
  color: var(--verydarkgrayishblue);
  padding: 0 4rem 0 1.5rem;
}
@media (min-width: 900px) {
  .accordion-item-body-content {
    padding: 0 7.625rem 1rem 1rem;
  }
}